import React, { Component } from 'react'
import ProgressBar from './ProgressBar';

export default class Languages extends Component {
    state = {
             languages:[
                {id: 1, value: "Javascript",xp:1.8}, 
                {id: 2, value: "Css",xp:2},
                {id: 3, value: "Php",xp:0.7},
                {id: 3, value: "Python",xp:0.4},
             ],
             frameworks:[
                {id: 1, value: "React",xp:1.4}, 
                {id: 2, value: "Boostrap",xp:2},
                {id: 3, value: "Sass",xp:1.5},
                {id: 3, value: "Materiel UI",xp:0.5},
             ]

    }
    render() {
        //on recupere nos 2 state sur {languages,frameworks}
        let {languages,frameworks}=this.state;
        return (
            <div className="languagesFrameworks">
                <ProgressBar
                languages={languages}
                className="languagesDisplay"
                titlle="languages"
                />
                <ProgressBar
                titlle="frameworks et bibiliotheques"
                 languages={frameworks}
                className="frameworksDisplay"
                
                />
                
            </div>
        )
    }
}
